.CoverPage {
  
  background: #fff;
  width: 100%;
  height: 100vh;
  max-height: 100vh;
  padding: 0;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 3fr 1fr 1fr 1fr 3fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.CoverPage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  overflow: hidden;
}
.CoverPage h1{
  width: 100%;
  text-align: center;
  font-size: 4em;
  color: black;
  font-family: var(--FontPrimary);
}
.CoverPage  .Img1 {
  grid-area: 1 / 1 / 2 / 2;
}
.CoverPage .Img2 {
  grid-area: 1 / 2 / 3 / 4;
}
.CoverPage .Img3 {
  grid-area: 4 / 1 / 6 / 3;
}
.CoverPage .Img4 {
  grid-area: 5 / 3 / 6 / 4;
}
.CoverPage h1 {
  grid-area: 3 / 1 / 4 / 4;
}
