main {
  z-index: 1;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  /* border: 5px solid var(--Color05);
  border-radius: 30px; */
  align-items: flex-end;
  justify-content: center;
  padding-right: 20px;
}

@media only screen and (max-width: 767px) {
  main {
    perspective: 700px;
  }
  main section {
    width: 80%;
    height: 90%;
    background: white;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 5px;
    border: 3px solid black;
    padding: 15px 5px 25px;
  }
  main section article {
    background: yellow;
    border: 3px solid black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  main section article h2 {
    font-weight: 900;
    font-size: 2.8em;
    text-align: center;
    text-decoration: underline;
    font-family: var(--FontPrimary);
    word-spacing: 2px;
    letter-spacing: 1px;
    color: white;
    /* -webkit-text-stroke: 1px black; */
    text-shadow: 1px 0 3px black, 0 1px 3px black, -1px 0 3px black,
      0 -1px 3px black;
  }
  main section article a {
    width: 100%;
    height: 2em;
    padding-top: 0.5em;
    font-size: 1em;
    color: white;
    text-decoration: none;
    background: rgb(0, 0, 0, 0.5);
    position: absolute;
    bottom: 0;
  }
  main section article span {
    position: absolute;
    top: 10px;
    right: 10px;
    background: black;
    padding: 2px 2px 15px;
    clip-path: polygon(      0% 0%,      100% 0%,      100% 75%,      75% 75%,      38% 100%,      48% 75%,      0% 75%    );
    animation-name: JuicyNew;
    animation-duration: 1s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
}
  @keyframes JuicyNew {
    0% {
      transform: rotateZ(0deg);
      -webkit-transform: rotateZ(0deg);
      -moz-transform: rotateZ(0deg);
      -ms-transform: rotateZ(0deg);
      -o-transform: rotateZ(0deg);
    }
    50% {
      transform: rotateZ(45deg);
      -webkit-transform: rotateZ(45deg);
      -moz-transform: rotateZ(45deg);
      -ms-transform: rotateZ(45deg);
      -o-transform: rotateZ(45deg);
}
    100% {
      transform: rotateZ(0deg);
      -webkit-transform: rotateZ(0deg);
      -moz-transform: rotateZ(0deg);
      -ms-transform: rotateZ(0deg);
      -o-transform: rotateZ(0deg);
    }
  }
  main section article span p {
    padding: 5px 5px 7px;
    width: 100%;
    background: white;
  }
  .Sketch1 {
    grid-area: 1 / 1 / 2 / 2;
    /* background: url("../components/img/pexels-martijn-adegeest-633565.jpg"); */
    /* clip-path: polygon(0 100%,0 0,50% 0, 100% 100%); */
  }

  .Sketch2 {
    grid-area: 1 / 2 / 2 / 4;
    /* clip-path: polygon(50% 100%,0 0,100% 0, 100% 100%); */
    /* background: url("../components/img/pexels-cottonbro-studio-5095880.jpg"); */
  }

  .Sketch3 {
    grid-area: 2 / 1 / 3 / 4;
    background: linear-gradient(to right, #ad5389, #3c1053);
  }

  .Sketch4 {
    grid-area: 3 / 1 / 4 / 3;
    /* clip-path: polygon(0 100%,0 0,50% 0, 100% 100%); */
    /* background: url("../components/img/pexels-cottonbro-studio-5095887.jpg"); */
  }

  .Sketch5 {
    grid-area: 3 / 3 / 4 / 4;
    /* background: url("../components/img/pexels-phil-s-2416463.jpg"); */
    /* clip-path: polygon(50% 100%,0 0,100% 0, 100% 100%); */
  }
  .Page {
    position: absolute;
    transform-origin: left center;
    transform-style: preserve-3d;
    transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 0.5s 0.5s;
    -webkit-transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 0.5s 0.5s;
    -moz-transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 0.5s 0.5s;
    -ms-transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 0.5s 0.5s;
    -o-transition: all cubic-bezier(0.25, 0.1, 0.25, 1) 0.5s 0.5s;
  }
  .Page.Down {
    transition-delay: 1s;
    transform: rotateY(-180deg);
    -webkit-transform: rotateY(-180deg);
    -moz-transform: rotateY(-180deg);
    -ms-transform: rotateY(-180deg);
    -o-transform: rotateY(-180deg);
  }
  .Page1 {
  }
  .Page2 {
  }
  .Page3 {
  }
}

