header {
  background: linear-gradient(to right bottom, var(--Color04), var(--Color01));
  width: 90%;
  display: flex;
  flex-direction: column;
}
header h1::selection,
header h2::selection,
header p::selection {
  background-color: var(--Color02);
  color: var(--Color05);
}
header h1 {
  font-family: var(--FontPrimary);
  font-weight: 900;
  color: var(--Color01);
  text-transform: uppercase;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}

header p span {
  font-family: var(--FontPrimary);
}

header p {
  font-family: var(--FontSecondary);
  font-size: 1.25em;
  font-weight: 500;
  color: #000;
}
header img {
  object-fit: cover;
  filter: grayscale(0.5);
  -webkit-filter: grayscale(0.8) drop-shadow(-1px -2px 25px black);
}

@media only screen and (max-width: 767px) {
  header {
    height: 500px;
    padding: 1.5em;
    margin-top: 40px;
    cursor: text;
    overflow: hidden;
    position: relative;
  }
  header h1 {
    margin-bottom: 0.75em;
    font-size: 2.5em;
    animation: text-animation 10s ease infinite;
    -webkit-animation: title-animation 10s ease infinite;
  }
  @keyframes title-animation {
    0% {
      letter-spacing: 1em;
      color: transparent;
    }
    50% {
      letter-spacing: 0.2em;
      color: black;
    }
    100% {
      letter-spacing: 1em;
      color: transparent;
    }
  }
  header p span {
    font-size: 1.5em;
    font-weight: 900;
    color: black !important;
}
  header p {
      text-align: justify;
      z-index: 2;
      animation: text-Animation 2s ease infinite;
      -webkit-animation: text-Animation 5s ease infinite;
}
  @keyframes text-Animation {
    0% {
      color: black;
      text-shadow: 1px 0 3px white, 0 1px 3px white, -1px 0 3px white,      0 -1px 3px white;
    }
    50% { 
      color: transparent;
      text-shadow: 0 0 0 transparent;
    }
    100% {
      color: black;
      text-shadow: 1px 0 3px white, 0 1px 3px white, -1px 0 3px white,      0 -1px 3px white;
    }
  }
  header img {
    position: absolute;
    bottom: 1em;
    right: -3em;
    width: 350px;
    z-index: 1;
  }
}

@media only screen and (min-width: 768px) {
}

@media only screen and (min-width: 1000px) and (any-hover: hover) {
  header:hover h1 {
    letter-spacing: 3px;
  }
  header:hover h2 {
    text-shadow: 1px 1px 2px var(--Color04);
  }
}
